
import { Component, OnInit, Output, EventEmitter,AfterViewInit } from '@angular/core';
import { DataService } from '../../services/data.service';

@Component({
  selector: 'app-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.css']
})
export class NavigationComponent implements OnInit, AfterViewInit {
    currentStatus="";
    @Output() 
    hide = new EventEmitter();
    constructor(public _dataService : DataService) { 
        _dataService.workStationStream$.subscribe(data => {
            console.log("update navigation")
            this.currentStatus='chooseContentType-query'
            // this.chooseContentType('query');
          });
          
    }
    ngAfterViewInit(){
    }
    close(){
        this.hide.next();
    }
    getTypeView(module){
        if(JSON.stringify(this._dataService.intentsIndexed[module])==JSON.stringify({})){
            return "(onlyresponse)"
        }
        else  if(this._dataService.journeysIndexed[module]==undefined){
            return "(noresponse)"
        }
        return ""
    }
    active(clickType){
        if(clickType==this.currentStatus){
            return "active"
        }
        return ""
    }
    showNextListArrow(event){
        this.showHideArrow(event.srcElement)
        this.folderViewOperation(event.srcElement.parentElement)
    }
    showNextList(event){
        for(let child of event.srcElement.childNodes){
            if(child.nodeName.startsWith("I")){
                this.showHideArrow(child)
            }
        }
        this.folderViewOperation(event.srcElement)
    }
    showHideArrow(element){
        if(element.classList.contains("fa-chevron-right")){
            element.classList.remove("fa-chevron-right")
            element.classList.add("fa-chevron-down")
        }
        else if(element.classList.contains("fa-chevron-down")){
            element.classList.remove("fa-chevron-down")
            element.classList.add("fa-chevron-right")
        }
    }
    folderViewOperation(element){
        if(element.nextElementSibling){
            if(element.nextElementSibling.hidden){
                for(let el of element.parentElement.childNodes){
                    if(el.nodeName.startsWith("UL")){
                        el.hidden=true
                    }
                //    el.classList.remove("active")
                }
                element.nextElementSibling.hidden=false
            }
            else{
                element.nextElementSibling.hidden=true
            }
        }
    }
    getArray(obj){
        let arr=[]
        for (let data in obj){
            arr.push(data)
        }
        return arr;
    }
    ngOnInit() {
        // this.currentStatus='chooseHeaderModule-ca-journeys'
        // this.chooseHeaderModule("ca","journeys");

    }
    addIntent(){
        console.log("chosen")
        this._dataService.doSelection({
            add:"intent"
          })
          this.currentStatus="addIntent"
    }
    addEntity(){
        console.log("chosen")
        this._dataService.doSelection({
            add:"entity"
          })
          this.currentStatus="addEntity"
    }


    chooseIntent(){
        console.log("chosen")
        this._dataService.doSelection({
            query:true,
            reply:true,
            journey:true,
            validate:true,
            invalid:true,
            cards:true,
            entity:false,
            module:false,
            folder:false,
            type:false
          })
          this.currentStatus="chooseIntent"
    }
    chooseEntity(){
        console.log("chosen")
        this._dataService.doSelection({
            query:false,
            reply:false,
            journey:false,
            validate:false,
            invalid:false,
            cards:false,
            entity:true,
            module:false,
            folder:false,
            type:false
          })
          this.currentStatus="chooseEntity"
    }
    chooseFolder(folder){
        console.log("chosen")
        this._dataService.doSelection({
            query:true,
            reply:true,
            journey:true,
            validate:true,
            invalid:true,
            cards:true,
            entity:false,
            module:false,
            folder:folder,
            type:false
          })
          this.currentStatus="chooseFolder-"+folder
    }
    chooseType(folder,type){
        console.log("chosen"+folder+type)
        this._dataService.doSelection({
            query:true,
            reply:true,
            journey:true,
            validate:true,
            invalid:true,
            cards:true,
            entity:false,
            module:false,
            folder:folder,
            type:type
          })
          this.currentStatus="chooseType-"+folder+"-"+type
    }
    chooseModuleIntent(cmodule,type){
        console.log("chosen")
        let select={
            query:false,
            reply:false,
            journey:false,
            validate:false,
            invalid:false,
            cards:false,
            entity:false,
            module:cmodule,
            folder:false,
            type:type,
            ui:true
          }
          if(type=="replies"){
            select["query"]=true;
            select["reply"]=true;
          }
          else if(type=="journeys"){
            select["query"]=true;
            select["journey"]=true;
            select["invalid"]=true;
            select["validate"]=true;
            select["cards"]=true;
          }
          else if(type=="onlyresponsejourney"){
            select["journey"]=true;
            select["invalid"]=true;
            select["validate"]=true;
            select["cards"]=true;
          }
          else if(type=="onlyresponsereply"){
            select["reply"]=true;
          }
          else if(type=="noresponse"){
            select["query"]=true;
          }
        this._dataService.doSelection(select)
          this.currentStatus="chooseModuleIntent-"+cmodule
    }
    chooseModuleEntity(cmodule){
        console.log("chosen")
        this._dataService.doSelection({
            query:false,
            reply:false,
            journey:false,
            validate:false,
            invalid:false,
            cards:false,
            entity:true,
            module:cmodule,
            folder:false,
            type:false,
            ui:true
          })
          this.currentStatus="chooseModuleEntity-"+cmodule
    }
    chooseHeaderModule(cmodule,header){
        console.log("chosen")
        let select={
            module:cmodule,
            folder:false
        }
        select["ui"]=true
        
        if(header=="journeys"){
            select["journey"]=true
            select["validate"]=true
            select["invalid"]=true
            select["cards"]=true
            select["type"]="journeys";
        }
        else{
            select[header]=true;
        }
        if(header!="query"){
            select["ui"]=true
        }
        if(select["reply"]&&!select["query"]){
            select["type"]="replies";
        }
        this._dataService.doSelection(select)
        this.currentStatus="chooseHeaderModule-"+cmodule+"-"+header
    }
    chooseContentType(type){
        let select={}
        if(type=="journeys"){
            select["journey"]=true
            select["validate"]=true
            select["invalid"]=true
            select["cards"]=true
            
        }
        else{
            select[type]=true;
        }
        this._dataService.doSelection(select)
        this.currentStatus="chooseContentType-"+type
    }
    deleteIntent(intent){
        delete this._dataService.intentsIndexed[intent]
        delete this._dataService.journeysIndexed[intent]
        this._dataService.toBeDeleted.intents.push(intent)
        this._dataService.syncProject();
    }
    deleteEntity(entity){
        delete this._dataService.entitiesIndexed[entity]
        this._dataService.toBeDeleted.entities.push(entity)
        this._dataService.syncProject();
    }
}

