import { Component, OnInit, AfterViewChecked, AfterViewInit, OnDestroy, Pipe, PipeTransform } from '@angular/core';
import { DataService } from '../../services/data.service';
// import { Observable, fromEvent } from 'rxjs';

@Component({
  selector: 'app-work-station',
  templateUrl: './work-station.component.html',
  styleUrls: ['./work-station.component.css']
})
export class WorkStationComponent implements OnInit, AfterViewInit, AfterViewChecked, OnDestroy {
  viewBody = [[]];
  viewHeader = [];
  cellChanged: any;
  cellInitValue: any;
  focusOnEl = {};
  queryPresenceIndex = 0;
  uiShowBody: any={
    query:{},
    journey:{},
    entity:{}
  }
  currentEditData:any;
  currentEdit:string;
  constructor(public _dataService: DataService) {

    this.currentEdit=" ";
    _dataService.workStationStream$.subscribe(data => {
      if (data.selection.query) {
        this.queryPresenceIndex = 1;
      }
      else {
        this.queryPresenceIndex = 0;
      }
      this.restructureViewBody(data);
      this.restructureShowBody(data);

    });


  }


  //ng events
  ngOnDestroy() {
  }
  ngAfterViewInit() {
  }
  ngOnInit() {
  }
  ngAfterViewChecked() {
  }


  //sync
  sync(){
    setTimeout(()=>{
      this._dataService.syncProject();
    },100)
  }

  //view change init restructure
  restructureShowBody(data){
    if(data.selection.module){
      if(data.selection.entity){
        this.uiShowBody.entity=data.entities[data.selection.module]
        // console.log(this.uiShowBody.entity)
      }  
      else if(data.selection.query){
        data.replies[data.selection.module]
        this.uiShowBody.query=data.intents[data.selection.module]
      }
      if(data.selection.reply){
        data.replies[data.selection.module]
        this.uiShowBody.journey=data.replies[data.selection.module];
      }
      else if(data.selection.journey){
        this.uiShowBody.journey=data.journeys[data.selection.module]
      }
    }
    
    // console.log(this.uiShowBody)
  
  }
  restructureViewBody(data){
    this.viewHeader = []
      for (let input of data.header) {
        if (this.viewable(data.selection, input)) {
          this.viewHeader.push(input);
        }
      }
      this.viewBody = [[]]
      let maxRowIndex = 0
      let maxColIndex = 0
      for (let rowIndex in data.body) {
        let row = data.body[rowIndex]
        let colIndex = 0
        this.viewBody.push([])
        for (let input of row) {
          if (this.viewable(data.selection, input)) {
            this.viewBody[rowIndex].push(input);
            if (!input.blank) {
              if (colIndex > maxColIndex) {
                maxColIndex = colIndex;
              }
              if (parseInt(rowIndex) > maxRowIndex) {
                maxRowIndex = parseInt(rowIndex);
              }
            }
          }
          colIndex++;
        }
      }
      this.viewBody = this.viewBody.slice(0, maxRowIndex + 1)
      for (let data of this.viewBody) {
        data = data.slice(0, maxColIndex + 1)
      }
  }  
  viewable(selection, obj) {
    if (!selection.type && !selection.folder && !selection.module) {
      if (selection[obj.header]) {
        return true
      }
      else {
        return false
      }
    }
    else if (selection.module && selection.module == obj.module) {
      if (selection[obj.header]) {
        return true
      }
      else {
        return false
      }
    }
    else if (selection.type && selection.folder) {
      if (selection.type && selection.type == obj.type) {
        if (selection.folder && selection.folder == obj.folder) {
          if (selection[obj.header]) {
            return true
          }
          else {
            return false
          }
        }
        else {
          return false
        }
      }
      else {
        return false
      }
    }
    else if (!selection.type && selection.folder && selection.folder == obj.folder) {
      if (selection[obj.header]) {
        return true
      }
      else {
        return false
      }
    }
    else {
      return false
    }
  }

  //helpers
  checkAdd(event,btn){
    if(this._dataService.intentsIndexed[event.srcElement.value]==undefined&&this._dataService.entitiesIndexed[event.srcElement.value]==undefined&&this._dataService.journeysIndexed[event.srcElement.value]==undefined){
      btn.disabled=false;
    }
    else{
      btn.disabled=true;
    }
    // console.log(event.srcElement.value)
  }
  getArray(obj) {
    let arr = []
    for (let data in obj) {
      arr.push(data)
    }
    return arr;
  }
  visibility(obj) {
    if (obj.blank) {
      return "true"
    }
    return "";
  }
  editable(obj) {
    if (obj.blank) {
      return false
    }
    return true;
  }
  changeValue(data) {
    // console.log(data.view)
  }
  replaceAll(str, find, replace) {
    return str.replace(new RegExp(find, 'g'), replace);
  }
  showEditView(showView, editView) {
    showView.hidden = true;
    editView.hidden = false;
    editView.focus();
  }
  focusEditView() {
    // console.log("focus")
  }
  focusInEditView() {
    // console.log("focus in")
  }
  focusOutEditElement(showView, editView, text) {
    // console.log("focus out" + text)
    if (text) {
      showView.hidden = false;
      editView.hidden = true;
    }
  }
  hideBlockElement(text,editBlock){
    return (text==''||text===undefined)||!editBlock.hidden
  }
  hideBubbleElement(text,editBlock){
    return !(text==''||text===undefined)||!editBlock.hidden
  }
  focusOutEditElementWithBubble(showView, editView, bubbleView, text) {
    // console.log("focus out" + text)
    if (text) {
      showView.hidden = false;
      editView.hidden = true;
    }
    else{
      bubbleView.hidden = false;
      editView.hidden = true;
    }
  }
  onClickNonEditableBlock(show,edit){
    edit.hidden=false;show.hidden=true;edit.focus()
  }
  onClickBubbleBlock(){

  }
  stringify(obj){
    return JSON.stringify(obj,null,3);
  }

  //query ui
  focusOnLastQuery(event){
    event.srcElement.parentElement.lastChild.focus();
  }
  checkQueryData(index){
    // console.log(index+"::::::::::::::>>>")
    if(this.uiShowBody.query.values[index].trim()==""){
      this.uiShowBody.query.values.splice(index, 1);
      
    }
  }
  addQuery(index){
    if(index!=undefined){
      // console.log(index)
      if(index==this.uiShowBody.query.values.length-1){
        this.uiShowBody.query.values.push(" ")
      }
    }
    else if(this.uiShowBody.query.values){
      this.uiShowBody.query.values.push(" ")
    }
    
  }

  //entity ui
  buildEntityText(dataArr,groupName){
    let value=""
    for(let val of dataArr){
      value+=val+";"
    }
    this.currentEditData={groupName:groupName}
    this.currentEdit=value;
    
    // console.log(this.currentEdit)
  }
  focusOutEditEntity(showView, editView) {
    // console.log("focus out")
    showView.hidden = false;
    editView.hidden = true;
    let synonyms = editView.innerText.toLowerCase().trim().replace(/\s*;$/g, "").trim().split(";")
    // console.log(synonyms)
    let index = 0;
    while (index < synonyms.length) {
      synonyms[index] = this.replaceAll(synonyms[index], /[^\w\d\s]/g, " ")
      // console.log(synonyms[index])
      let synonym = synonyms[index] = synonyms[index].trim();
      if (synonym == "") {
        synonyms.splice(index, 1)
      }
      else {
        index++;
      }
    }
    this.uiShowBody.entity.values[this.currentEditData.groupName]=synonyms
    this.currentEdit=" "
    
    // console.log(synonyms)
    
  }
  addEntity(index){
    if(index!=undefined){
      // console.log(index)
      if(index==this.uiShowBody.entity.values.length-1){
        this.uiShowBody.entity.values[" "]=[]
      }
    }
    else if(this.uiShowBody.entity.values){
      this.uiShowBody.entity.values[" "]=[]
    }
    
  }
  saveSynName(data,groupName,entityElement){
    let value=data[groupName];
    delete data[groupName];
    if(entityElement){
      data[entityElement.innerText]=value;
      this.currentEditData={groupName:entityElement.innerText}
    }
    if (entityElement.innerText.trim()==""){
      delete data[entityElement.innerText];
    }
    
  }

  //journey ui
  addStage(){

    this.uiShowBody.journey.stages.push({
      text: [
        ""
      ],
      type: "text",
      stage: "stage"+this.uiShowBody.journey.stages.length
    })
    
  }
  queryPresent() {
    if (this.queryPresenceIndex === 1) {
      return ""
    }
    return "query-not-present"
  }
  getCardText(stage){
    if(stage.next&&stage.next.data){
      if(stage.type=="quickReply"&&stage.next.data.length>0){
        return "exists"
      }
      if(stage.type=="button"){
        return "exists"
      }
    }
    else if(stage.next&&stage.next.pre&&stage.next.pre.length>0){
      return "exists"
    }
    return ""
  }
  getValidateText(stage){
    if (stage.next && stage.next.post&&stage.next.post[0].url) {
        return "exists"
    }
    else if (stage.next && stage.next.expectation && stage.next.expectation.val) {
        return "exists"
    }
    return ""
  }
  selectCards(stage,type){
    if(type=='api'){
      if(!stage["next"]){
        stage["next"]={pre:[]}
      }
      stage.type="text";
      stage["next"]["data"]=undefined
      stage["next"]["pre"]=[
          {
            type: type,
            respStates: {
                Success: "200",
                Failed: "203"
            },
            requestType: "POST",
            url: ""
          }
        ]
    }
    else{
      stage.type=type;
      if(!stage["next"]){
        stage["next"]={data:[]}
      }
      stage["next"]["pre"]=undefined
      stage["next"]["data"]=[]
    }
    
  }
  buildCardsText(stage){
    if(stage.next&&stage.next.data&&stage.type){
      if(stage.type=="quickReply"){
        let view=""
        for(let card of stage.next.data){
          view+=card.text+";"
        }
        return view;
      }
      if(stage.type=="button"){
        let view=""
        for(let card of stage.next.data){
          if(card.type=="webView"){
            view+="webView:";
          }
          else if(card.type=="url"){
            view+="url:";
          }
          view+=card.text+">"+card.data+";"
        }
        return view;
      }
    }
    else if(stage.next&&stage.next.pre){
      return stage.next.pre[0].url
    }
    

  }
  selectValidate(stage,type){
    if(type=='api'){
      if(!stage["next"]){
        stage["next"]={post:[]}
      }
      stage["next"]["expectation"]=undefined
      stage["next"]["post"]=[
        {
          type: type,
          respStates: {
              Success: "200",
              Failed: "203"
          },
          invalidMessage: "",
          validMessage: "",
          requestType: "POST",
          url: ""
        }
      ]
      
    }
    else{

      if(!stage["next"]){
        stage["next"]={expectation:[]}
      }
      stage["next"]["post"]=undefined
      stage["next"].expectation= {
            invalidMessage: "",
            validMessage: "",
            val: undefined,
            type: type
      }
      
    }
    
  }
  buildValidateText(stage){
    let view
    if (stage.next && stage.next.post) {
        view = stage.next.post[0].url
    }
    else if (stage.next && stage.next.expectation && stage.next.expectation.type == 'wordList') {
        view = ""
        for (let val in stage.next.expectation.val) {
            if (stage.next.expectation.val[val]) {
                view += val + "-" + stage.next.expectation.val[val] + ";"
            }
            else {
                view += val + ";"
            }
        }
    }
    else if (stage.next && stage.next.expectation && stage.next.expectation.type == 'regex') {
        view = stage.next.expectation.val
    }
    
    return view;

  }
  closeEditCards(editableArea,editable,cardsBlockShow,empty,stage,event)
  {
    // console.log("focusout "+editableArea.innerText)
    editable.hidden = true;
    event.srcElement.parentElement.hidden=true;
    // console.log(editable.hidden)

      if(stage.next&&stage.type=="quickReply"){
        let cardsData = editableArea.innerText.trim().replace(/\s*;$/g, "").trim().split(";")
        let index = 0;
        let data=[]
        while (index < cardsData.length) {
          let value=cardsData[index].trim();
          if (!(value == "")) {
            data.push({
              data:value,
              text:value
            })
          }
          index++;
        }
        if(data.length>0&&editableArea.innerText.trim()){
          stage["next"].data=data
        }
        else{
          stage.type="text";
          stage["next"].data=undefined
        }
      }
      else if(stage.next&&stage.type=="button"){
        // console.log("______________________________")
        let cardsData = editableArea.innerText.trim().replace(/\s*;$/g, "").trim().split(";")
        // console.log(cardsData)
        let index = 0;
        let data=[]
        while (index < cardsData.length) {
          let value=cardsData[index].trim();
          if(cardsData[index].split(">").length>1){
            let typeSave;
            let dataSave=cardsData[index].split(">")[1];
            let textSave=cardsData[index].split(">")[0];
            if(value.startsWith('webView:')){
              typeSave="webView";
              textSave=textSave.replace('webView:',"").trim();
            }
            else if(value.startsWith('url:')){
              typeSave="url";
              textSave=textSave.replace('url:',"").trim();
            }
            data.push({
              type:typeSave,
              data:dataSave,
              text:textSave
            })
          }
          else{
            data.push({
              data:value,
              text:value
            })
          }
          index++;
        }
        // console.log(data)
        if(cardsData.length>0&&editableArea.innerText.trim()){
          stage["next"].data=data
        }
        else{
          stage.type="text";
          stage["next"].data=undefined
        }
      }
      else if(stage.next.pre){
        if(editableArea.innerText.trim()){
          stage.type="text";
          stage.next.pre[0].url = editableArea.innerText.trim()
        }
        else{
          stage.type="text";
          stage["next"].pre=undefined;
        }
        
      }
      if(this.getCardText(stage)==""){
        empty.hidden=false;
      }
      else{
        cardsBlockShow.hidden=false;
      }
      
  } 
  checkJourneyData(stage,index){
      if(stage.stage.trim()==""&&stage.text[0].trim()==""){
        this.uiShowBody.journey.stages.splice(index,1)
        
      }
      
  }
  checkFirstData(stage,index){
      if(stage.stage.trim()==""&&(!stage.firstMessage||stage.firstMessage.trim()=="")){
        this.uiShowBody.journey.stages.splice(index,1)
        
      }
  }
  closeEditValidate(editableArea,editable,validateBlockShow,empty,stage,event)
  {
    // console.log("focusout "+editableArea.innerText)
    editable.hidden = true;
    event.srcElement.parentElement.hidden=true;
    // console.log(editable.hidden)

    if(stage.next.expectation&&stage.next.expectation.type){
      if(editableArea.innerText.trim()){
        switch(stage.next.expectation.type){
          case "regex":stage.next.expectation.val=editableArea.innerText;
                break;
          case "wordList":  let validateData = editableArea.innerText.trim().replace(/\s*;$/g, "").trim().split(";")
                            let index = 0;
                            let data=[]
                            stage.next.expectation.val={}
                            while (index < validateData.length) {
                              let value=validateData[index].trim();
                              if(validateData[index].split("-").length>1){
                                let stageSave=validateData[index].split("-")[1];
                                let entitySave=validateData[index].split("-")[0];
                                stage.next.expectation.val[entitySave]=stageSave
                              }
                              else{
                                stage.next.expectation.val[value]=""
                              }
                              index++;
                              
                            }

                break;
          default:  stage.next.expectation=undefined;
                    break;

        }
      }
      else{
          stage.next.expectation=undefined;
      }
    }
    else if(stage.next.post){
      if(editableArea.innerText){
        stage.next.post[0].url = editableArea.innerText
      }
      else{
        stage.next.post=undefined;
      }
      
    }
    if(this.getValidateText(stage)==""){
      // console.log("emptyyyyyyyyyyy")
      empty.hidden=false;
    }
    else{
      // console.log("blockkkk")
      validateBlockShow.hidden=false;
    }

    
   
  }

  //delete stage
  deleteStage(i){
    this.uiShowBody.journey.stages.splice(i, 1);
    
  }

  //swap stage
  swapStage(i,j){
    if(i>=0&&j<this.uiShowBody.journey.stages.length){
      let temp=this.uiShowBody.journey.stages[i]
      this.uiShowBody.journey.stages[i]=this.uiShowBody.journey.stages[j]
      this.uiShowBody.journey.stages[j]=temp;
    }
    
  }

  //add data
  addNewIntent(el){
    this._dataService.intentsIndexed[el.value]={values:[],intentId:el.value}
    this._dataService.journeysIndexed[el.value]={stages:[{
              text: [
                ""
              ],
              type: "text",
              stage: "stage1"
            }],
            flowId:el.value
          }
    this._dataService.doSelection({query:true,module:el.value,ui:true})
    el.value=""
    
  }

  addNewEntity(el){
    this._dataService.entitiesIndexed[el.value]={values:{},entityId:el.value}
    // console.log(this._dataService.entitiesIndexed[el.value])
    this._dataService.doSelection({entity:true,module:el.value,ui:true})
    el.value=""
    
  }
  

}



@Pipe({ name: 'keys' })
export class KeysPipe implements PipeTransform {
  transform(value, args: string[]): any {
    let keys = [];
    for (let key in value) {
      keys.push(key);
    }
    return keys;
  }
}