import { Component, OnInit,Output,EventEmitter, Input } from '@angular/core';
import {DataService} from '../../services/data.service';
import {
  AuthService,
  FacebookLoginProvider,
  GoogleLoginProvider
} from 'angular-6-social-login';
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  @Output() 
  hideBot = new EventEmitter();
  @Output() 
  showBot = new EventEmitter();
  @Output() 
  logout = new EventEmitter();
  @Input()
  botStatus;
  shareHidden:boolean = true;
  // conflictHidden:boolean = true;
  // conflictData="";
  emailId:string;
  constructor(private _dataService: DataService, private socialAuthService: AuthService) { }

  ngOnInit() {
  }
  toggleBot(){
    if(this.botStatus){
      this.hideBot.next();
    }
    else{
      this.showBot.next();

    }
  }
  saveBot(){
    this._dataService.saveBot();
  }
  // pullAndPush(){
  //   this._dataService.pullAndPush();
  // }
  // pushAndPull(){
  //   this._dataService.pushAndPull();
  // }
  // closeConflictView(){
  //   this.conflictHidden=true
  // }
  publishBot(){
    this._dataService.publishBot();
  }
  showClassText(){
    if(this.botStatus){
      return "close"
    }
    return "test"
  }
  draft(){
    this._dataService.getSyncProject()
  }

  viewAllProjects(){
    this._dataService.deregisterProject();
  }

  logoutClick(){
    this.socialAuthService.signOut();
    this.logout.next();
  }
  toggleShareOption(){
    console.log("share button clicked");
    this.shareHidden = !this.shareHidden;
  }
  share(){
    this._dataService.share(this.emailId);
  }
  getSpreadsheetLink(){
    try{
      return this._dataService.user.data.projects[this._dataService.projectId].spreadsheetUrl;
    }
    catch(e){
      return "#"
    }
  }
}
