import { Component, OnInit, AfterViewChecked, AfterViewInit, OnDestroy, Pipe, PipeTransform } from '@angular/core';
import { DataService } from '../../services/data.service';

@Component({
  selector: 'app-all-projects',
  templateUrl: './all-projects.component.html',
  styleUrls: ['./all-projects.component.scss']
})
export class AllProjectsComponent implements OnInit {
  newProjectName:string="";
  constructor(public _dataService: DataService) { 
  }

  getUserProjectNames(){
    if(this._dataService.user&&this._dataService.user.data){
      return Object.keys(this._dataService.user.data.projects);
    }
    else{
      return [];
    }
  }

  selectProject(projectId){
    this._dataService.getProject(projectId)
  }
  createProject(){
    let invalid=false
    if(this._dataService.user&&this._dataService.user.projects){
      for(let project of Object.keys(this._dataService.user.projects)){
        if(this.newProjectName==project){
          invalid=true;
          break;
        }
      }
    }
    this.newProjectName=this.newProjectName+"_"+this._dataService.user.id
    // console.log(this.newProjectName)
    if(this.newProjectName&&this.newProjectName!=""&&!invalid){
      this._dataService.createProject(this.newProjectName);
      this.newProjectName=""
    }
  }

  ngOnInit() {
  }

}
